import React from "react";
import styles from "./Footer.module.css";

const footer = (props) => {
    return(
        <footer className={styles.footer}>
        <div className="footer__links">
            <ul className={styles.footer_list}>
                <li className={styles.footer_item}><a href="#footer" className={styles.footer_item_link}>Career</a></li>
                <li className={styles.footer_item}><a href="#footer" className={styles.footer_item_link}>Legal Notice</a></li>
                <li className={styles.footer_item}><a href="https://yusufmert.netlify.com" rel="noopener noreferrer" target="_blank"className={styles.footer_item_link}>Contact</a></li>       
            </ul>
        </div>
        <div className={styles.logo_box}> 
            <img src={require("../../assets/logo.png")} alt="logo" className={styles.logo}/>
        </div>
        <div className={styles.text}>
            <p className={styles.copyright}>
                    Built by Yusuf Mert Çakmak. Copyright © by Yusuf Mert Çakmak. You are 100% allowed to use this webpage for both personal and commercial use, but NOT to claim it as your own design. A credit to the original author, Yusuf Mert Çakmak, is of course highly appreciated!</p>
        </div>
    </footer>        
    )
}

export default footer;