import React from "react";
import styles from "./Rooms.module.css"
const style1=styles.card + " " + styles.card_2;
const style2=styles.card + " " + styles.card_1;
const style3=styles.card + " " + styles.card_3;


const Rooms = (props) =>{
    return(
        
        <div className={styles.rooms_container}>
                <div className={styles.rooms} id="rooms">
                    <div className={style1}>
                        <div className={styles.card_text}>Single Room</div>
                        <div className={styles.card_price}>160$</div>
                        <div className={styles.card_buy}><a href="#intro" className={styles.card_buy_link}>Buy Now</a></div>
                    </div>
                    
                    <div className={style2}>
                        <div className={styles.card_text}>Family Room</div>
                        <div className={styles.card_price}>200$</div>
                        <div className={styles.card_buy}><a href="#intro" className={styles.card_buy_link}>Buy Now</a></div>
                    </div>
                    <div className={style3}>
                        <div className={styles.card_text}>Suite Room</div>
                        <div className={styles.card_price}>300$</div>
                        <div className={styles.card_buy}><a href="#intro" className={styles.card_buy_link}>Buy Now</a></div>
                    </div>
                </div>
            </div>
    )
}

export default Rooms;