import React from "react";
import styles from "./Nav.module.css"
const nav = (props) => {
    return(
        <div className={styles.heading}>
            <div className={styles.logobox}>
                <a href="#intro"><img src={require("../../assets/logo.png")} alt="logo" className={styles.logo}/></a>
            </div>
            <div className={styles.nav}>
                <ul className={styles.list}>
                    <li className={styles.list_item}><a href="#intro" className={styles.nav_link}>Home</a></li>
                    <li className={styles.list_item}><a href="#rooms" className={styles.nav_link}>Rooms</a></li>
                    <li className={styles.list_item}><a href="https://yusufmert.netlify.com" rel="noopener noreferrer" target="_blank" className={styles.nav_link}>Creator</a></li>
                </ul>
            </div>
        </div>
       
    )
}

export default nav;